import React, { useEffect, useRef } from "react";
import throttle from 'lodash.throttle';

import Nav from "./Nav";
import About from "./About";
import Works from "./Works";
import Notes from "./Notes";
import Contact from "./Contact";

const MainContent = () => {
    const navRef = useRef(null);

    useEffect(() => {
        const link = document.createElement('a');
        link.href = '#about'; // Your URL
        link.click();
    });

    const handleScroll = () => {
        const documentY = window.screenY + 10;

        try{
            let el = document.getElementById("contact");
            if(el.getBoundingClientRect().y < documentY){
                navRef.current.handleFocus(4);
                return;
            }

            el = document.getElementById("notes")
            if(el.getBoundingClientRect().y < documentY){
                navRef.current.handleFocus(3);
                return;
            }

            el = document.getElementById("works");
            if(el.getBoundingClientRect().y < documentY){
                navRef.current.handleFocus(2);
                return;
            }

            el = document.getElementById("about");
            if(el.getBoundingClientRect().y < documentY){
                navRef.current.handleFocus(1);
            }
        }catch (e){
            /* Moved to another page when scrolling */
        }
    }

    const controlledScroll = throttle(handleScroll, 16)

    return(
        <div id="mainContent" onScroll={ controlledScroll }>
            <Nav ref={ navRef }/>
            <About/>
            <Works/>
            <Notes/>
            <Contact/>
        </div>
    );
};

export default React.memo(MainContent);