export default "### LeetCode 32 Longest Valid Parentheses, hard\n" +
"\n" +
"##### [Link to the problem](https://leetcode.com/problems/longest-valid-parentheses/)\n" +
"\n" +
"##### Method\n" +
"\n" +
"* First version\n" +
"  \n" +
"  \n" +
"  \n" +
"  Below is a sample string comprising with '(' and ')'\n" +
"  \n" +
"  (())((())))())\n" +
"  \n" +
"  Start from the left most character.\n" +
"  \n" +
"  It's a left parenthesis, start checking. \n" +
"  \n" +
"  **(**())((())))())\n" +
"  \n" +
"  Read until right parenthesis is more than left parenthesis. During this progress, when left and right counts equal, save the length.\n" +
"  \n" +
"  -(())-((())))())\n" +
"  \n" +
"  -(())((()))-)())\n" +
"  \n" +
"  (())((()))**)**())\n" +
"  \n" +
"  Check next.\n" +
"  \n" +
"  it's a right parenthesis, go next.\n" +
"  \n" +
"  (())((())))**(**))\n" +
"  \n" +
"  Same procedure.\n" +
"  \n" +
"  (())((())))-()-)\n" +
"  \n" +
"  (())((())))()**)**\n" +
"  \n" +
"  It should work with O(n) since every character is only visited once.\n" +
"  \n" +
"  **What's the Problem**\n" +
"  \n" +
"  If the left parenthesis is more than right parenthesis, need to repeat the checking for that part.\n" +
"  \n" +
"  An extreme situation is\n" +
"  \n" +
"  ((((((((((((((((((((((((((((\n" +
"  \n" +
"  Every character here will trigger the check once, and the program will read through the whole string.\n" +
"  \n" +
"  With the maximum limit of input length, it may run more than 10 seconds.\n" +
"  \n" +
"  \n" +
"\n" +
"* Final version\n" +
"  \n" +
"  \n" +
"  \n" +
"  Still do the same check as above. But after the iteration, do the check in reversed order.\n" +
"  \n" +
"  In whatever situation, it will iterate the string twice. Still O(n) complexity.\n" +
"\n" +
"##### Code\n" +
"\n" +
"* Final version\n" +
"  \n" +
"  ```java\n" +
"  public static int longestValidParentheses(String s) {\n" +
"      int length = s.length(), max = 0, mark = 0;\n" +
"  \n" +
"      int index = 0, left = 0, right = 0;\n" +
"      while (index < length){\n" +
"          if(s.charAt(index) == '('){\n" +
"              left++;\n" +
"          }else{\n" +
"              right++;\n" +
"          }\n" +
"  \n" +
"          if(right > left){\n" +
"              left = 0; right = 0;\n" +
"              mark = index;\n" +
"          }\n" +
"  \n" +
"          if(left == right){\n" +
"              max = Math.max(max, left + right);\n" +
"          }\n" +
"  \n" +
"          index++;\n" +
"      }\n" +
"  \n" +
"      index = length - 1;\n" +
"      left = 0;\n" +
"      right = 0;\n" +
"  \n" +
"      while (index > mark){\n" +
"          if(s.charAt(index) == '('){\n" +
"              left++;\n" +
"          }else{\n" +
"              right++;\n" +
"          }\n" +
"  \n" +
"          if(right < left){\n" +
"              left = 0; right = 0;\n" +
"          }\n" +
"  \n" +
"          if(left == right){\n" +
"              max = Math.max(max, left + right);\n" +
"          }\n" +
"  \n" +
"          index--;\n" +
"      }\n" +
"  \n" +
"      return max;\n" +
"  }\n" +
"  ```\n" +
"\n" +
"* First version\n" +
"  \n" +
"  ```java\n" +
"  public static int longestValidParentheses_oldVersion(String s) {\n" +
"      int length = s.length(), max = 0;\n" +
"  \n" +
"      int left = 0;\n" +
"      while(left < length){\n" +
"          // If it's (, start check, read until ) is more than (.\n" +
"          if (s.charAt(left) == '(') {\n" +
"              int right = left + 1, count = 1, len = 0;\n" +
"  \n" +
"              while (right < length) {\n" +
"                  if (s.charAt(right) == '(') {\n" +
"                      count++;\n" +
"                  } else {\n" +
"                      count--;\n" +
"                  }\n" +
"  \n" +
"                  if(count < 0){\n" +
"                      // Finish checking, set pointer to left after right\n" +
"                      left = right;\n" +
"                      break;\n" +
"                  } else{\n" +
"                      if (count == 0) {\n" +
"                          len = right - left + 1;\n" +
"                      }\n" +
"  \n" +
"                      right++;\n" +
"                  }\n" +
"              }\n" +
"  \n" +
"              max = Math.max(max, len);\n" +
"          }\n" +
"  \n" +
"          left++;\n" +
"      }\n" +
"  \n" +
"      return max;\n" +
"  }\n" +
"  ```\n" +
"\n" +
"* Main method to check the time diff\n" +
"  \n" +
"  ```java\n" +
"  public static void main(String[] args){\n" +
"      StringBuffer temp = new StringBuffer();\n" +
"      temp.append(\"(\".repeat(100000));\n" +
"      temp.append(\")\".repeat(50000));\n" +
"  \n" +
"      long start = System.currentTimeMillis();\n" +
"      System.out.println(\"Result:\" + longestValidParentheses(temp.toString()));\n" +
"      long end = System.currentTimeMillis();\n" +
"  \n" +
"      start = System.currentTimeMillis();\n" +
"      System.out.println(\"Result:\" + longestValidParentheses_oldVersion(temp.toString()));\n" +
"      end = System.currentTimeMillis();\n" +
"  \n" +
"      System.out.println(end - start);\n" +
"  }\n" +
"  ```";