import React from "react";

class Preview extends React.Component{
    constructor(props) {
        super(props);

        this.img = props.img;

        this.state = { showing: 1, countDown: 4 };
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            if(this.img.length < 3)
                return;

            const countDown = this.state.countDown - 1;
            if(countDown === 0){
                if(this.state.showing < this.img.length - 1) {
                    this.handleToggle(this.state.showing + 1);
                }else{
                    this.handleToggle(1);
                }
            }else{
                this.setState({ countDown });
            }
        }, 1000);
    }

    componentWillUnmount() {
        clearInterval(this.timer);
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return nextState.showing !== this.state.showing;
    }

    handleToggle = (value) => {
        if(this.state.showing === value){
            return;
        }

        this.setState({ showing: value, countDown: 4 });
    }

    render() {
        const showing = this.state.showing;

        return(
            <div className='preview'>
                <img className="previewImg" src={ this.img[showing] } alt="Preview not available"/>

                <div className="dots">
                    {
                        this.img.map((_, index) => {
                            if(index === 0){
                                return null;
                            }
                            return (<div className={"dot" + (showing===index?" focus":"")} key={ index } onClick={ () => this.handleToggle(index) }>
                            </div>);
                        })
                    }
                </div>
            </div>
        );
    }
}

export default Preview;