import React from "react";
import { Link } from "react-router-dom";

import icon from "../sources/iconShallow.png";

class Nav extends React.Component{
    constructor(props) {
        super(props);

        this.state = { selected: 0 };
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return this.state.selected !== nextState.selected;
    }

    handleFocus = (target) => {
        this.setState({ selected: target });
    }

    render() {
        const selected = this.state.selected;

        return (
            <div id="nav">
                <ul>
                    <HomeBtn/>
                    <a href="#about" className={ selected===1?"focus":"" }><li key="about" value="about">About</li></a>
                    <a href="#works" className={ selected===2?"focus":"" }><li key="works" value="works">Works</li></a>
                    <a href="#notes" className={ selected===3?"focus":"" }><li key="notes" value="notes">Notes</li></a>
                    <a href="#contact" className={ selected===4?"focus":"" }><li key="contact" value="contact">Contact</li></a>
                </ul>
                <img id="navIcon" src={ icon } alt=""/>
            </div>
        );
    }
}

const HomeBtn = React.memo(() => <Link to="/"><li key="home" value="home">Home</li></Link>);

export default Nav;