import React from "react";

const fullContent = "Discover";

class Discover extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            content: "",
            direction: true,
            pause: 0
        };
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return this.state.content !== nextState.content;
    }

    componentDidMount() {
        this.animation = setInterval(() => {
            const state = { ...this.state };
            if(state.pause){
                this.setState({ pause: state.pause - 1 });
                return;
            }
            let newContent;
            if(this.state.direction){
                newContent = fullContent.substring(0, state.content.length+1);
                state.pause = 1;
                if(newContent.length === 8){
                    state.pause = 8;
                    state.direction = false;
                }
            }else{
                newContent = fullContent.substring(0, state.content.length-1);
                if(newContent.length === 0) {
                    state.pause = 4;
                    state.direction = true;
                }
            }
            state.content = newContent;
            this.setState(state);
        }, 120);
    }

    componentWillUnmount() {
        clearInterval(this.animation);
    }

    render() {
        let temp = [];
        for (let ch of this.state.content){
            temp.push(<span key={ ch }>{ ch }</span>);
        }

        return (<div id="discover">{ temp }<span id="discoverEnd" key={ "end" }>|</span></div>);
    }
}

export default Discover;