import React from "react";
import photo from "../sources/photo.png";

const About = () => {
    return (
        <div id="about" className="page">
            <div className="space">
            </div>
            <div className="title">About</div>
            <div id="aboutContent">
                <div>
                    <img src={ photo } alt=""/>
                </div>
                <div>
                    <p>I'm a software engineer. Previously, I was an undergrad majoring in computer information science, minoring in Statistic and Psychology at the Ohio State University. My interest lies in human-computer interaction and data Visualization. </p>
                </div>
            </div>
        </div>
    );
};

export default React.memo(About);